.home {
  max-width: 1500px;
  margin: auto;
}

.home__row {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  margin: 0 5px;
}

.home__image {
  width: 100%;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  margin: auto;
  z-index: -1;
  margin-bottom: -150px;
}
